@Composable
internal fun App(
    systemAppearance: (isLight: Boolean) -> Unit = {}
) = AppTheme(systemAppearance) {
    TabNavigator(HomeTab) {
        Scaffold(
            bottomBar = {
                AnimVisible(Constants.isBottomBarVisible) {
                    NavigationBar {
                        BottomItem(HomeTab)
                        BottomItem(SaveTab)
                        BottomItem(SettingsTab)
                    }
                }
            }
        ) { CurrentTab() }
    }

}















@Composable
fun Preview() {
    var email by remember { mutableStateOf("") }
    var password by remember { mutableStateOf("") }
    var passwordVisibility by remember { mutableStateOf(false) }

    Column(modifier = Modifier.fillMaxSize().windowInsetsPadding(WindowInsets.safeDrawing)) {

        Row(
            horizontalArrangement = Arrangement.Center
        ) {
            Text(
                text = "Login",
                style = MaterialTheme.typography.titleMedium,
                modifier = Modifier.padding(16.dp)
            )

            Spacer(modifier = Modifier.weight(1.0f))

            ThemeSwitcher()
        }

        OutlinedTextField(
            value = email,
            onValueChange = { email = it },
            label = { Text("Email") },
            singleLine = true,
            modifier = Modifier.fillMaxWidth().padding(16.dp)
        )

        OutlinedTextField(
            value = password,
            onValueChange = { password = it },
            label = { Text("Password") },
            singleLine = true,
            visualTransformation = if (passwordVisibility) VisualTransformation.None else PasswordVisualTransformation(),
            modifier = Modifier.fillMaxWidth().padding(16.dp),
            keyboardOptions = KeyboardOptions(
                keyboardType = KeyboardType.Password
            ),
            trailingIcon = {
                IconButton(onClick = { passwordVisibility = !passwordVisibility }) {
                    val imageVector = if (passwordVisibility) Icons.Default.Close else Icons.Default.Edit
                    Icon(imageVector, contentDescription = if (passwordVisibility) "Hide password" else "Show password")
                }
            }
        )

        Button(
            onClick = { /* Handle login logic here */ },
            modifier = Modifier.fillMaxWidth().padding(16.dp)
        ) {
            Text("Login")
        }

        TextButton(
            onClick = { openUrl("https://github.com/terrakok") },
            modifier = Modifier.fillMaxWidth().padding(16.dp)
        ) {
            Text("Open github")
        }
    }
}

@Composable
fun ThemeSwitcher() {
    var isDark by LocalThemeIsDark.current
    IconButton(
        onClick = { isDark = !isDark }
    ) {
        Icon(
            modifier = Modifier.padding(8.dp).size(20.dp),
            imageVector = if (isDark) rememberLightMode() else rememberDarkMode(),
            contentDescription = null
        )
    }
}

internal expect fun openUrl(url: String?)