package com.nirwashh.newskmp.data.repository

import com.nirwashh.newskmp.data.mapper.toNews
import com.nirwashh.newskmp.data.network.KtorService
import com.nirwashh.newskmp.domain.model.News
import com.nirwashh.newskmp.domain.repository.NetworkRepository
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.flow

class NetworkRepositoryImpl(
    private val ktorService: KtorService
) : NetworkRepository {

    override suspend fun getNews(query: String): Flow<List<News>> = flow {
        val response = ktorService.getNews(query)
        emit(response.articles.map { it.toNews() })
    }
}