package com.nirwashh.newskmp.data.network

import com.nirwashh.newskmp.data.model.NewsResponse
import com.nirwashh.newskmp.utils.Constants
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.request.get
import io.ktor.client.request.parameter

class KtorClient(
    private val httpClient: HttpClient
) : KtorService {
    override suspend fun getNews(query: String): NewsResponse {
        return httpClient.get(Constants.BASE_URL) {
            parameter("apiKey", Constants.API_KEY)
            parameter("q", query)
        }.body()
    }
}