package com.nirwashh.newskmp.presentation.home

import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.padding
import androidx.compose.material3.Icon
import androidx.compose.material3.Scaffold
import androidx.compose.material3.Tab
import androidx.compose.material3.TabRow
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.State
import androidx.compose.runtime.collectAsState
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import cafe.adriel.voyager.core.screen.Screen
import cafe.adriel.voyager.navigator.LocalNavigator
import cafe.adriel.voyager.navigator.currentOrThrow
import com.nirwashh.newskmp.domain.model.News
import com.nirwashh.newskmp.presentation.components.ContentList
import com.nirwashh.newskmp.presentation.components.Loading
import com.nirwashh.newskmp.presentation.components.NormalTopAppBar
import com.nirwashh.newskmp.presentation.components.TabItem
import com.nirwashh.newskmp.presentation.details.DetailsScreen
import org.koin.compose.rememberKoinInject

internal class HomeScreen(
    private val state: State<HomeState>,
    private val loadNews: (Int) -> Unit
) : Screen {
    @Composable
    override fun Content() {
        val tabs = listOf(TabItem.General, TabItem.Business, TabItem.Technology)
        var selectedIndex by remember { mutableStateOf(0) }
        val navigator = LocalNavigator.currentOrThrow

        Scaffold(
            topBar = {
                NormalTopAppBar(
                    title = "News",
                    onOpen = {

                    }
                )
            }
        ) {
            Column(
                modifier = Modifier.padding(it).padding(bottom = 80.dp)
            ) {
                TabRow(
                    selectedTabIndex = selectedIndex
                ) {
                    tabs.forEachIndexed { index, tabItem ->
                        Tab(
                            selected = selectedIndex == index,
                            onClick = {
                                selectedIndex = index
                                loadNews(selectedIndex)
                            },
                            icon = {
                                Icon(tabItem.icon, contentDescription = null)
                            },
                            text = {
                                Text(text = tabItem.label)
                            }
                        )
                    }
                }

                if (state.value.isLoading) Loading()
                if (state.value.success.isNotEmpty()) {
                    ContentList(
                        list = state.value.success[selectedIndex],
                        onClick = { news ->
                            navigator.push(DetailsScreen(news))
                        }
                    )
                }

            }
        }
    }
}