package com.nirwashh.newskmp.presentation.home

import androidx.compose.runtime.mutableStateListOf
import com.nirwashh.newskmp.domain.model.News
import com.nirwashh.newskmp.domain.use_case.AllUseCase
import dev.icerock.moko.mvvm.viewmodel.ViewModel
import kotlinx.coroutines.CoroutineExceptionHandler
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.SupervisorJob
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.catch
import kotlinx.coroutines.flow.collectLatest
import kotlinx.coroutines.flow.onStart
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch

class HomeViewModel(
    private val allUseCase: AllUseCase
) {
    private val _state = MutableStateFlow(HomeState())
    val state get() = _state.asStateFlow()

    private val generalList = mutableListOf<News>()
    private val businessList = mutableListOf<News>()
    private val technologyList = mutableListOf<News>()

    private val coroutineContext = SupervisorJob() + Dispatchers.Default

    private val scope = CoroutineScope(coroutineContext)

    init {
        loadNews(0)
    }

    fun loadNews(index: Int) {
        when(index) {
            0 -> {
                if (generalList.isEmpty()) fetchNews("general", generalList)
            }
            1 -> {
                if (businessList.isEmpty()) fetchNews("business", businessList)
            }
            else -> {
                if (technologyList.isEmpty()) fetchNews("technology", technologyList)
            }
        }
    }

    private fun fetchNews(query: String, newList: MutableList<News>) {
        scope.launch {
            allUseCase.getNewUseCase(query)
                .onStart {
                    _state.update {
                        it.copy(isLoading = true)
                    }
                }.catch { t->
                    _state.update {
                        it.copy(isLoading = false,  error = (t.message.toString()))
                    }
                }.collectLatest { list ->
                    newList.addAll(list)
                    _state.update {
                        it.copy(
                            isLoading = false,
                            success = listOf(generalList, businessList, technologyList)
                        )
                    }
                }
        }
    }
}