package com.nirwashh.newskmp.domain.use_case

import com.nirwashh.newskmp.domain.model.News
import com.nirwashh.newskmp.domain.repository.NetworkRepository
import kotlinx.coroutines.flow.Flow
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

typealias GetNewBaseUseCase = BaseUseCase<String, Flow<List<News>>>

class GetNewUseCase : KoinComponent, GetNewBaseUseCase {
    private val repo by inject<NetworkRepository>()
    override suspend fun invoke(parameter: String): Flow<List<News>> = repo.getNews(parameter)
}