package com.nirwashh.newskmp.di

import com.nirwashh.newskmp.data.network.KtorClient
import com.nirwashh.newskmp.data.network.KtorService
import com.nirwashh.newskmp.data.repository.NetworkRepositoryImpl
import com.nirwashh.newskmp.domain.repository.NetworkRepository
import com.nirwashh.newskmp.domain.use_case.AllUseCase
import com.nirwashh.newskmp.domain.use_case.GetNewUseCase
import com.nirwashh.newskmp.presentation.home.HomeViewModel
import io.ktor.client.HttpClient
import io.ktor.client.plugins.contentnegotiation.ContentNegotiation
import io.ktor.serialization.kotlinx.json.json
import kotlinx.serialization.json.Json
import org.koin.core.module.dsl.bind
import org.koin.core.module.dsl.singleOf
import org.koin.dsl.module

val appModule = module {
    single {
        HttpClient {
            install(ContentNegotiation) {
                json(Json {
                    ignoreUnknownKeys = true
                    useAlternativeNames = false
                })
            }
        }
    }

    singleOf(::KtorClient) {
        bind<KtorService>()
    }

    singleOf(::NetworkRepositoryImpl) {
        bind<NetworkRepository>()
    }
    factory {
        AllUseCase(
            getNewUseCase = GetNewUseCase()
        )
    }
    factory {
        HomeViewModel(get())
    }
}