package com.nirwashh.newskmp.data.mapper

import com.nirwashh.newskmp.data.model.Article
import com.nirwashh.newskmp.domain.model.News

fun Article.toNews(): News {
    return News(
        author = author,
        content = content,
        description = description,
        publishedAt = publishedAt,
        sourceName = source?.name,
        sourceId = source?.id,
        title = title,
        url = url,
        urlToImage = urlToImage
    )
}